body {
  margin: 0;
  padding: 0;
  font-family: 'Bubbler One', sans-serif;
  text-align: center;
  color: white;
  background-color: #1f3860;
}

h1 {
  margin-top: 60px;
}

.search-bar {
  max-width: 400px;
  margin: 60px auto;
}

.current {
  text-align: left;
  margin: 100px auto;
  margin-bottom: 60px;
  width: 400px;
  font-size: 1.1em;
}


.left {
  margin-right: 120px;
  text-align: left;
  float: left;
}

.forecast {
  margin-top: 150px;
  margin-bottom: 100px;
}


@media (min-width: 768px){
  .seven-cols .col-md-1,
  .seven-cols .col-sm-1,
  .seven-cols .col-lg-1  {
    width: 14.285714285714285714285714285714%;
    *width: 14.285714285714285714285714285714%;
  }
}




